#about{
  display: flow-root;
}
.aboutbox article:first-child {
  border-bottom: 1px solid white;
  /* text-align: center; */
  padding-bottom: 50px; 
}
.aboutbox>article:not(.bio){
  padding-top: 50px;
}
.bio .date{
float:left;
}
.skills .date{
float:right;
}
.skills>p span{
 color: rgba(255, 0, 180, 1);
  font-size: larger;
  font-weight: bolder;
  font-family: 'Julius Sans One', sans-serif;
}
.date span {
  font-family: "Lobster", cursive;
  font-size: 3em;
  line-height: 90px;
  color: white;
}
.date {
  border: 4px gold dotted;
  shape-outside: circle(95px);
  width: 90px;
  height: 90px;
  border-radius: 90px;
  text-align:center;
  margin: 50px;
  /* background-color: gold; */
}

