@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One|Josefin+Slab|Open+Sans:300|Cabin+Sketch|Fredericka+the+Great|Lobster|Quicksand&display=swap);
.home{
  overflow: hidden;
  width: 100%;
  font-family: 'Fredericka the Great', cursive;
}
.mail{display: none}
.full {
  height: 100vh;
  display: grid;
  grid-template-rows:  auto repeat(3,1fr);
  grid-template-columns: repeat(3,1fr);
  grid-gap: 15px;
}
.full > a:not(.welcome) {
  font-family: 'Julius Sans One', sans-serif;
  text-shadow: 5px 5px 0 rgba(255, 0, 180, 0.5);
  font-size:2.3em;
  color:#ff7214;
  z-index:2;
  width: 50%;
  place-self: center;
}
.full > a:hover{
  color: yellow;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.aboutme {
  grid-area: 2 / 1 / 2 / 2;
}
.work {
  grid-area: 3 / 1 / 3 / 2;
}
.contactNav{
  grid-area:4 / 1 / 4 / 2;
}
.iconbar{
  grid-area: 1 / 1 / 1 / 4;
  display: flex;
  font-family: 'Josefin Slab', serif;
  background-color:white;
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  color: #ff7214;
}
.iconbar i{
  position: relative;
  display:flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  border: 2px dotted transparent;
  border-radius: 50%;
  height: 80px; 
  width: 80px;
  margin: 10px;
  z-index: 2;
  color: #ff7214;
}
.iconbar i:hover {
  color: rgba(255, 0, 180, 0.5);
}
.iconbar i:hover{
  border-top-color: navy;
  border-right-color: navy;
  border-bottom-color: navy;
  border-left-color: navy;
  -webkit-transition: border-top-color 0.15s linear, border-right-color 0.15s linear 0.1s, border-bottom-color 0.15s linear 0.2s,  border-left-color 0.15s linear 0.3s;
  transition: border-top-color 0.15s linear, border-right-color 0.15s linear 0.1s, border-bottom-color 0.15s linear 0.2s,  border-left-color 0.15s linear 0.3s;
}
.welcome {
  grid-area: 1 / 1 / 5/ 4;
  background-image: url(/portfolio/static/media/rose-egg.d5dc20f0.svg);
  background-size: contain;
  background-position: right;
  background-repeat: no-repeat;
  z-index:1; 
}
.welcomeP{
  grid-area: 2 / 3 / 5/ 4;
  z-index:2;
  font-family: 'Julius Sans One', sans-serif;
  text-shadow: 5px 5px 0 rgba(255, 0, 180, 0.5);
  color:navy;
  font-size:3.5em;
  text-align: right;
  border-right: 5px dotted navy;
  padding-right:20px;
  margin:20px;
  align-self:center;
  line-height: 1.1em;
}
#trademark{
grid-area: 4 / 1 / 4 / 4;
height: -webkit-fit-content;
height: -moz-fit-content;
height: fit-content;
font-family: 'Josefin Slab', serif;
font-weight: bolder;
letter-spacing: 5px;
align-self:end;
justify-self: center;
color: white;
width: 100%;
text-align: center;
}
.trademark{
  
  padding: 50px;
}
#about{
  display: flow-root;
}
.aboutbox article:first-child {
  border-bottom: 1px solid white;
  /* text-align: center; */
  padding-bottom: 50px; 
}
.aboutbox>article:not(.bio){
  padding-top: 50px;
}
.bio .date{
float:left;
}
.skills .date{
float:right;
}
.skills>p span{
 color: rgba(255, 0, 180, 1);
  font-size: larger;
  font-weight: bolder;
