.row{
  margin: 8px -16px;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.row,
.row > .column {
  padding: 8px;
}
.column{
  float: left;
  width: 50%;
}
.projectbox{
  position:relative;
  height: 500px;
  padding:10px;
  text-align:center;
}
.projectbox img {
  background-color:white;
  /* object-fit:cover; */
  /* height:500px; */
  width:100%;
  -webkit-transition:opacity ease-out 250ms;
  transition:opacity ease-out 250ms;
  opacity:1;
}
.projectbox:hover img{
  opacity:0.1;
}
.projectbox::before{
  content:"";
  position:absolute;
  top:1.25em;
  bottom:1.25em;
  left:1.25em;
  right:1.25em;
  -webkit-transform:scale(0,1);
          transform:scale(0,1);
  -webkit-transition: -webkit-transform ease-out 250ms;
  transition: -webkit-transform ease-out 250ms;
  transition: transform ease-out 250ms;
  transition: transform ease-out 250ms, -webkit-transform ease-out 250ms;
  border-bottom:1px solid white;
}
.projectbox::after{
  content:"";
  position:absolute;
  top:1.25em;
  bottom:1.25em;
  left:1.25em;
  right:1.25em;
  -webkit-transform:scale(1,0);
          transform:scale(1,0);
  -webkit-transition: -webkit-transform ease-out 250ms;
  transition: -webkit-transform ease-out 250ms;
  transition: transform ease-out 250ms;
  transition: transform ease-out 250ms, -webkit-transform ease-out 250ms;
  border-left: 1px solid white;
}
.projectbox:hover::before{
  -webkit-transform: scale(1.05,1);
          transform: scale(1.05,1);
}
.projectbox:hover::after{
  -webkit-transform: scale(1,1.05);
          transform: scale(1,1.05);
  }
.projectbox ul{
  position:absolute;
  top:50%;
  left:50%; 
  -webkit-transform: translate(-50%,-50%); 
          transform: translate(-50%,-50%);
  opacity:0;
  z-index: 1;
  -webkit-transition: opacity ease-out 250ms;
  transition: opacity ease-out 250ms;
}
.projectbox:hover ul{
  opacity:1;
}
.projectbox li p{
  text-align: center;
}
